import { styled } from "@rp/react/theme"
import { useDeviceInfo } from "@rp/react/web/common/hooks"

const IconWrapper = styled("div", { shouldForwardProp: prop => prop !== "isMobile" })<{
  isMobile: boolean
}>(({ theme, isMobile }) => ({
  paddingInline: theme.spacing(1.25),

  "::after": {
    content: "''",
    position: "absolute",
    height: isMobile ? 25 : 10,
    top: isMobile ? -25 : -10,
    width: 1,
    backgroundColor: isMobile ? theme.palette.grey[500] : theme.palette.grey[300],
  },
}))

const DefaultTreeIcon = styled("div", { shouldForwardProp: prop => prop !== "isMobile" })<{
  isMobile: boolean
}>(({ theme, isMobile }) => ({
  "::before": {
    content: "''",
    position: "absolute",
    height: 1,
    width: 10,
    backgroundColor: isMobile ? theme.palette.grey[500] : theme.palette.grey[300],
  },

  "::after": {
    content: "''",
    position: "absolute",
    height: "100%",
    width: 1,
    backgroundColor: isMobile ? theme.palette.grey[500] : theme.palette.grey[300],
  },
}))

const LastTreeIcon = styled("div", { shouldForwardProp: prop => prop !== "isMobile" })<{
  isMobile: boolean
}>(({ theme, isMobile }) => ({
  "::before": {
    content: "''",
    position: "absolute",
    height: 1,
    width: 10,
    backgroundColor: isMobile ? theme.palette.grey[500] : theme.palette.grey[300],
  },

  "::after": {
    content: "''",
    position: "absolute",
    height: "50%",
    top: 0,
    width: 1,
    backgroundColor: isMobile ? theme.palette.grey[500] : theme.palette.grey[300],
  },
}))

type Props = {
  type: "default" | "last"
}

const NestedSuggestionIcon = ({ type }: Props) => {
  const { isMobile } = useDeviceInfo()
  return (
    <IconWrapper isMobile={isMobile} data-testid="nested-suggestion-icon">
      {type === "default" ? (
        <DefaultTreeIcon isMobile={isMobile} />
      ) : (
        <LastTreeIcon isMobile={isMobile} />
      )}
    </IconWrapper>
  )
}

export { NestedSuggestionIcon }
